<style scoped lang="less">
  @import "../../../../css/recordLayout";
</style>
<template>
  <div class="pendingRecord content-layout">
    <div class="content-header">
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="病理号"></el-table-column>
        <el-table-column prop="b" label="姓名"></el-table-column>
        <el-table-column prop="c" label="性别"></el-table-column>
        <el-table-column prop="d" label="年龄"></el-table-column>
        <el-table-column prop="e" label="送检部位"></el-table-column>
        <el-table-column prop="f" label="送检类型"></el-table-column>
        <el-table-column prop="g" label="送检时间" width="150"></el-table-column>
        <el-table-column prop="h" label="流程状态"></el-table-column>
        <el-table-column label="操作" width="150">
          <template scope="scope">
            <el-button type="text" size="small" @click="lookRecord(scope.$index, tableData)">查看</el-button>
            <el-button type="text" size="small" @click="withdraw(scope.$index, tableData)">撤回</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <look-record v-show="showRecord" :recordData="recordData"  @getChildMessage="closeRecord"></look-record>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import lookRecord from '../../../public/lookRecord'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: '',
          starttimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          endtime: '',
          endtimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          zt: [
            {value: '不限',label: '不限'},
            {value: '普通',label: '普通'},
            {value: '加急',label: '加急'},
          ],
          ztValue: '',
          fl: [
            {value: '不限',label: '不限'},
            {value: '全科',label: '全科'},
            {value: '软组织',label: '软组织'},
            {value: '皮肤',label: '皮肤'},
            {value: '消化系统',label: '消化系统'},
            {value: '腹膜及腹膜后',label: '腹膜及腹膜后'},
            {value: '妇产科',label: '妇产科'},
            {value: '骨和关节',label: '骨和关节'},
            {value: '泌尿系统',label: '泌尿系统'},
            {value: '鼻、咽、眼、耳、喉',label: '鼻、咽、眼、耳、喉'},
            {value: '甲状腺',label: '甲状腺'},
            {value: '乳腺',label: '乳腺'},
            {value: '口腔、唇、舌、额骨',label: '口腔、唇、舌、额骨'},
            {value: '呼吸系统',label: '呼吸系统'},
            {value: '男性生殖系统',label: '男性生殖系统'},
            {value: '淋巴造血系统',label: '淋巴造血系统'},
            {value: '神经系统',label: '神经系统'},
            {value: '涎腺',label: '涎腺'},
            {value: '纵隔',label: '纵隔'},
            {value: '肝胆',label: '肝胆'},
            {value: '胸膜',label: '胸膜'},
            {value: '内分泌',label: '内分泌'},
            {value: '胰腺',label: '胰腺'},
            {value: '心血管',label: '心血管'},
          ],
          flValue: '',
          zj: [
            {value: '不限',label: '不限'},
            {value: '鸿琪专家',label: '鸿琪专家'},
            {value: '邵建永',label: '邵建永'},
            {value: '唐录英',label: '唐录英'},
            {value: '郜红益',label: '郜红益'},
            {value: '刘勇',label: '刘勇'},
            {value: '王朝夫',label: '王朝夫'},
            {value: '梅开勇',label: '梅开勇'},
            {value: '路名芝',label: '路名芝'},
            {value: '邵春奎',label: '邵春奎'},
            {value: '吕建民',label: '吕建民'},
          ],
          zjValue: '',
          seachInfo: '',
        },
        tableData: [
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待诊断'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '腹部', f: '普通', g: '2016-05-02 11:11', h: '待分配'}
        ],
        currentPage: 1,
        recordData: {},//病历信息
        showRecord: false,//病历信息显示隐藏
      }
    },
    props: [''],
    components: {
      headerSearch,
      lookRecord
    },
    mounted(){
      this.seachData.ztValue = this.seachData.zt[0].label
      this.seachData.flValue = this.seachData.fl[0].label
      this.seachData.zjValue = this.seachData.zj[0].label
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },

      seachFn(val){
        console.log(this.seachData)
      },
      lookRecord(i,arr){
        this.showRecord = true;
        this.recordData = {
          radio: '1'
        }
      },
      closeRecord(val) {
        //evtValue 关闭  是子组件传过来的值关闭病历信息组件
        this.showRecord = val
      },
      withdraw(i,arr){
        this.$confirm('此操作将撤回病历, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
//          arr.splice(i, 1);
          this.$message({
            type: 'success',
            message: '撤回成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消撤回'
          });
        });
      },
    }
  }
</script>







