          {a: '00001', b: '李四', c: '男', d: '紧急'},
          {a: '00001', b: '李四', c: '男', d: '紧急'},
          {a: '00001', b: '李四', c: '男', d: '紧急'},
          {a: '00001', b: '李四', c: '男', d: '紧急'},
          {a: '00001', b: '李四', c: '男', d: '紧急'}
        ],
      }
    },
    props: ['a'],
    components: {

    },
    mounted(){

    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      aas(i,arr){}
    }
  }
</script>



