<style scoped lang="less">
@import "../../../../css/recordLayout";
</style>
<template>
  <div class="retreatRecord content-layout">
    <div class="content-header">
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="a" label="病理号"></el-table-column>
        <el-table-column prop="b" label="姓名"></el-table-column>
        <el-table-column prop="c" label="性别"></el-table-column>
        <el-table-column prop="d" label="年龄"></el-table-column>
        <el-table-column prop="e" label="临床诊断"></el-table-column>
        <el-table-column prop="f" label="送检物"></el-table-column>
        <el-table-column prop="g" label="送检时间" width="150"></el-table-column>
        <el-table-column label="操作" width="200">
          <template scope="scope">
              <el-button type="text" size="small" @click="retreatLook(scope.$index, tableData)">查看</el-button>
            <el-button type="text" size="small"   @click="retreatEdit(scope.$index, tableData)">编辑</el-button>
            <el-button type="text" size="small"   @click="retreatback(scope.$index, tableData)">退单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <retreat-addedit v-show="editshowRecord" :recordData="editrecordData"  @getChildMessage="editcloseRecord"></retreat-addedit>
    </transition>
    <transition name="el-fade-in">
      <retreat-look v-show="lookshowRecord" :recordData="lookrecordData"  @getChildMessage="lookcloseRecord"></retreat-look>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import retreatAddedit from '../../../public/addeditRecord'
  import retreatLook from '../../../public/lookRecord'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: '',
          starttimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          endtime: '',
          endtimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          zt: [
            {value: '不限',label: '不限'},
            {value: '普通',label: '普通'},
            {value: '加急',label: '加急'},
          ],
          ztValue: '',
          fl: [
            {value: '不限',label: '不限'},
            {value: '全科',label: '全科'},
            {value: '软组织',label: '软组织'},
            {value: '皮肤',label: '皮肤'},
            {value: '消化系统',label: '消化系统'},
            {value: '腹膜及腹膜后',label: '腹膜及腹膜后'},
            {value: '妇产科',label: '妇产科'},
            {value: '骨和关节',label: '骨和关节'},
            {value: '泌尿系统',label: '泌尿系统'},
            {value: '鼻、咽、眼、耳、喉',label: '鼻、咽、眼、耳、喉'},
            {value: '甲状腺',label: '甲状腺'},
            {value: '乳腺',label: '乳腺'},
            {value: '口腔、唇、舌、额骨',label: '口腔、唇、舌、额骨'},
            {value: '呼吸系统',label: '呼吸系统'},
            {value: '男性生殖系统',label: '男性生殖系统'},
            {value: '淋巴造血系统',label: '淋巴造血系统'},
            {value: '神经系统',label: '神经系统'},
            {value: '涎腺',label: '涎腺'},
            {value: '纵隔',label: '纵隔'},
            {value: '肝胆',label: '肝胆'},
            {value: '胸膜',label: '胸膜'},
            {value: '内分泌',label: '内分泌'},
            {value: '胰腺',label: '胰腺'},
            {value: '心血管',label: '心血管'},
          ],
          flValue: '',
          zj: [
            {value: '不限',label: '不限'},
            {value: '鸿琪专家',label: '鸿琪专家'},
            {value: '邵建永',label: '邵建永'},
            {value: '唐录英',label: '唐录英'},
            {value: '郜红益',label: '郜红益'},
            {value: '刘勇',label: '刘勇'},
            {value: '王朝夫',label: '王朝夫'},
            {value: '梅开勇',label: '梅开勇'},
            {value: '路名芝',label: '路名芝'},
            {value: '邵春奎',label: '邵春奎'},
            {value: '吕建民',label: '吕建民'},
          ],
          zjValue: '',
          seachInfo: '',
        },
        tableData: [
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'},
          {a: '00001', b: '李四', c: '男', d: '90', e: '公主病', f: '脾气', g: '2016-05-02 11:11'}
        ],
        currentPage: 1,
        editshowRecord: false,//编辑
        editrecordData:{},
        lookshowRecord: false,//查看
        lookrecordData: {},
      }
    },
    props: [''],
    components: {
      headerSearch,
      retreatAddedit,
      retreatLook
    },
    mounted(){
      this.seachData.ztValue = this.seachData.zt[0].label
      this.seachData.flValue = this.seachData.fl[0].label
      this.seachData.zjValue = this.seachData.zj[0].label
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      seachFn(val){
        console.log(this.seachData)
      },

      /*操作*/
      retreatLook(i,arr){
        this.lookshowRecord = true;
      },
      lookcloseRecord(){
          /*关闭查看*/
        this.lookshowRecord = false;
      },
      retreatEdit(i,arr){
        this.editshowRecord = true;
      },
      editcloseRecord(){
        /*关闭编辑*/
        this.editshowRecord = false;
      },
      retreatback(i,arr){
        this.$prompt('请输入放弃此病历原因', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /\S/,
          inputErrorMessage: '内容不能为空'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '退单成功'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      },
    }
  }
</script>



