<style scoped lang="less">
@nav-title-height: 50px;
@nav-inner-height: 40px;
.pt-nav {
  width: 100%;
  .ul {
    width: 100%;
    .ulchind {
      width: 100%;
      text-align: left;
      text-indent: 1em;
      .ultxt {
        display: block;
        height: @nav-title-height;
        line-height: @nav-title-height;
        width: 100%;
        background: #eef9ff;
        color: #666;
        font-size: 18px;
        cursor: default;
        font-family: "宋体";
        i {
          color: #1D8CE0;
        }
      }
      .ol {
        width: 100%;
        display: none;
        .olchild {
          width: 100%;
          height: @nav-inner-height;
          line-height: @nav-inner-height;
          text-align: left;
          text-indent: 3em;
          cursor: default;
          font-size: 16px;
          background: rgb(216, 241, 255);
          color: #666;
          &:hover,
          &.navact{
            background: #7eb3ea;
            color: #fdf8f8;
          }
        }
      }
    }
  }
}
</style>
<template>
  <div class="pt-nav">
    <ul class="ul">
      <li class="ulchind" v-for="(item,index) in navdata">
        <span class="ultxt"><i class="el-icon-menu" :style="{'margin-right':'5px'}"></i>{{item.name}}</span>
        <ol class="ol">
          <router-link activeClass="navact" v-for="list in item.child" :key="list" class="olchild" :to="list.router" tag="li">{{list.text}}</router-link>
        </ol>
      </li>
    </ul>
    <!--router-link中使用:key是因为不使用会有webpack热加载报一个警告。在:to里面需要用到list调用里面的值。在这里是没有申明的。直接使用会报警告。用:key申明一下list可以使用就不报警告了-->
  </div>
</template>
<script>
  import $ from 'jquery'
  export default {
    name: 'pt-nav',
    data(){
      return {

      }
    },
    props: ['navdata'],
    mounted(){
      $(".navact").parent(".ol").slideDown();
      $(".ul li span").click(function () {
        if($(this).siblings("ol").css("display") == "block"){
          $(this).siblings('.ol').slideUp();
        }else {
          $('.ul li .ol').slideUp();
          $(this).siblings('.ol').slideDown();
        }
        /*如果不要点击收起来的效果。就把if语句里面的删除。保留else语句里面的内容*/
      })
    },
    methods: {

    }
  }
</script>







