<style scoped lang="less"></style>
<template>
  <div class="outlay layout">
    <div class="layout-header">
      <h1 @click="toMenu">LOGO</h1>
      <div class="user">
        <div class="user-info">
          <el-dropdown trigger="click"  @command="handleCommand">
            <span class="el-dropdown-link">
              计费端<i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="账号设置">账号设置</el-dropdown-item>
              <el-dropdown-item command="修改密码">修改密码</el-dropdown-item>
              <el-dropdown-item command="退出登录">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="layout-main">
      <div class="layout-main-nav">
        <pt-nav-view :navdata="navInfo"></pt-nav-view>
      </div>
      <div class="layout-main-info">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  import ptNavData from '../../api/pt-nav.js'
  import ptNavView from '../public/pt-nav.vue'
  export default {
    name: 'outlay',
    data(){
      return {
        navInfo: ptNavData.outlay,
      }
    },
    components: {
      ptNavView
    },
    methods: {
      toMenu(){
        this.$router.push({path: '/outlayMenu'})
      },
      handleCommand(command) {
        if(command == "退出登录"){
          this.$router.push({path: '/'})
        }else {
          this.$message('点击了' + command + '没有其他的操作');
        }
      }
    }
  }
</script>



