<style scoped lang="less">
	.Index {
		width:  100%;
		height: 100%;
    font-family: "SimSun", "宋体";
    position: relative;
    .Index-info {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 280px;
      height: 200px;
      h1 {
        font-weight: 600;
        color: #333;
        cursor: default;
      }
      span {
        display: inline-block;
        margin-top: 8px;
        font-size: 24px;
        cursor: pointer;
        color: #8bb6bf;
      }
    }
	}
</style>
<template>
<div class="Index">
  <div class="Index-info">
    <h1 @click="toIndex">{{msg}}</h1>
    <br>
    <h1 @click="toIndex">远程病理诊断平台</h1>
    <br>
    <span @click="toLogin">登录</span>
  </div>
</div>
</template>
<script>
export default {
  name: 'index',
  data () {
    return {
      msg: '人仁医'
    }
  },
  methods: {
    toLogin(){
        this.$router.push({path: '/login'});
    },
    toIndex(){
        this.$router.push({path: '/'});
    }
  }
}
</script>



