/*
* 当前为配置路由文件---api下的pt-nav.js文件为执行分发路由文件
* */

import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import login from '@/components/login/login'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: index},
    {path: '/login', component: login},
    {path: '/hospitalMenu', component: require('@/components/menu/hospitalMenu')},
    {path: '/expertMenu', component: require('@/components/menu/expertMenu')},
    {path: '/allocatingMenu', component: require('@/components/menu/allocatingMenu')},
    {path: '/releaseMenu', component: require('@/components/menu/releaseMenu')},
    {path: '/manageMenu', component: require('@/components/menu/manageMenu')},
    {path: '/outlayMenu', component: require('@/components/menu/outlayMenu')},
    {
      path: '/hospital',//医院端
      component: require('@/components/hospital/hospital'),
      children: [
        {path: '/hospital',                   component: require('@/components/hospital/children/routine/addRecord')},           //常规病历
        {path: '/hospital/addRecord',         component: require('@/components/hospital/children/routine/addRecord')},           //常规病历---新建病历
        {path: '/hospital/pendingRecord',     component: require('@/components/hospital/children/routine/pendingRecord')},       //常规病历---待诊病历
        {path: '/hospital/alreadyRecord',     component: require('@/components/hospital/children/routine/alreadyRecord')},       //常规病历---已诊病历
        {path: '/hospital/retreatRecord',     component: require('@/components/hospital/children/routine/retreatRecord')},       //常规病历---退回病历

        {path: '/hospital/addConsult',        component: require('@/components/hospital/children/consult/addConsult')},          //会诊病历---新建会诊
        {path: '/hospital/InProcessConsult',  component: require('@/components/hospital/children/consult/InProcessConsult')},    //会诊病历---处理中会诊
        {path: '/hospital/alreadyConsult',    component: require('@/components/hospital/children/consult/alreadyConsult')},      //会诊病历---已处理会诊

        {path: '/hospital/pendingYYFrozen',   component: require('@/components/hospital/children/frozen/pendingYYFrozen')},      //冰冻病历---待预约冰冻
        {path: '/hospital/pendingCLFrozen',   component: require('@/components/hospital/children/frozen/pendingCLFrozen')},      //冰冻病历---待处理冰冻
        {path: '/hospital/alreadyHandle',     component: require('@/components/hospital/children/frozen/alreadyHandle')},        //冰冻病历---已处理冰冻

        {path: '/hospital/statistics',        component: require('@/components/hospital/children/statistics/statistics')},       //统计查询---统计
        {path: '/hospital/query',             component: require('@/components/hospital/children/statistics/query')},            //统计查询---查询

        {path: '/hospital/templateMaintain',  component: require('@/components/hospital/children/templateMaintain/templateMaintain')},//模板维护---模板维护
      ]
    },
    {
      path: '/expert',//专家端
      component: require('@/components/expert/expert'),
      children: [
        {path: '/expert',                    component: require('@/components/expert/children/schedule/schedule')},              //日程安排
        {path: '/expert/schedule',           component: require('@/components/expert/children/schedule/schedule')},              //日程安排--日程安排

        {path: '/expert/stayDiagnosis',      component: require('@/components/expert/children/routine/stayDiagnosis')},          //常规病历--待诊病历
        {path: '/expert/alreadyDiagnosis',   component: require('@/components/expert/children/routine/alreadyDiagnosis')},       //常规病历--已诊病历

        {path: '/expert/beingProcessed',     component: require('@/components/expert/children/consult/beingProcessed')},         //会诊病历--处理中
        {path: '/expert/alreadyHandleConsult',component: require('@/components/expert/children/consult/alreadyHandle')},          //会诊病历--已处理

        {path: '/expert/frozenOrder',        component: require('@/components/expert/children/frozen/frozenOrder')},             //冰冻病历--冰冻预约
        {path: '/expert/stayHandle',         component: require('@/components/expert/children/frozen/stayHandle')},              //冰冻病历---待处理冰冻
        {path: '/expert/alreadyHandleFrozen',component: require('@/components/expert/children/frozen/alreadyHandle')},           //冰冻病历---已处理冰冻

        {path: '/expert/statistics',         component: require('@/components/expert/children/statistics/statistics')},          //统计查询---统计
        {path: '/expert/query',              component: require('@/components/expert/children/statistics/query')},               //统计查询---查询
      ]
    },
    {
      path: '/allocating',//分配端
      component: require('@/components/allocating/allocating'),
      children: [
        {path: '/allocating',                     component: require('@/components/allocating/children/pending/stayDistributionBL')},       //待处理病历
        {path: '/allocating/stayDistributionBL',  component: require('@/components/allocating/children/pending/stayDistributionBL')},       //待处理病历--待分配病历
        {path: '/allocating/ReferralApply',       component: require('@/components/allocating/children/pending/ReferralApply')},            //待处理病历--转诊申请
        {path: '/allocating/repeatApply',         component: require('@/components/allocating/children/pending/repeatApply')},              //待处理病历--复审申请
        {path: '/allocating/delayApply',          component: require('@/components/allocating/children/pending/delayApply')},               //待处理病历--延迟申请
        {path: '/allocating/overtimeRecord',      component: require('@/components/allocating/children/pending/overtimeRecord')},           //待处理病历--超时病历

        {path: '/allocating/alreadyAssigned',     component: require('@/components/allocating/children/alreadyAssigned/alreadyAssigned')},  //已分配病历--已分配病历

        {path: '/allocating/frozenYY',            component: require('@/components/allocating/children/frozen/frozenYY')},                  //冰冻--冰冻预约
        {path: '/allocating/frozenYYSuccess',     component: require('@/components/allocating/children/frozen/frozenYYSuccess')},           //冰冻--预约成功
        {path: '/allocating/frozenInvitation',    component: require('@/components/allocating/children/frozen/frozenInvitation')},          //冰冻--冰冻邀请
        {path: '/allocating/CompletedFrozen',     component: require('@/components/allocating/children/frozen/CompletedFrozen')},           //冰冻--已完成冰冻

        {path: '/allocating/stayDistributionHZ',  component: require('@/components/allocating/children/consult/stayDistributionHZ')},       //会诊--待分配
        {path: '/allocating/HZInvitation',        component: require('@/components/allocating/children/consult/HZInvitation')},             //会诊--会诊邀请
        {path: '/allocating/HZing',               component: require('@/components/allocating/children/consult/HZing')},                    //会诊--会诊中
        {path: '/allocating/alreadyHZ',           component: require('@/components/allocating/children/consult/alreadyHZ')},                //会诊--已会诊

        {path: '/allocating/statistics',          component: require('@/components/allocating/children/statistics/statistics')},            //统计查询--统计
        {path: '/allocating/query',               component: require('@/components/allocating/children/statistics/query')},                 //统计查询--查询
      ]
    },
    {
      path: '/release',//发布端
      component: require('@/components/release/release'),
      children: [
        {path: '/release',                   component: require('@/components/release/children/stayRelease')},                              // 发布端
        {path: '/release/stayRelease',       component: require('@/components/release/children/stayRelease')},                              // 发布端--待发布
        {path: '/release/alreadyRelease',    component: require('@/components/release/children/alreadyRelease')},                           // 发布端--已发布
        {path: '/release/againRelease',      component: require('@/components/release/children/againRelease')},                             // 发布端--重新发布
      ]
    },
    {
      path: '/outlay',//计费端
      component: require('@/components/outlay/outlay'),
      children: [
        {path: '/outlay',                   component: require('@/components/outlay/children/statistics/statistics')},                             // 发布端--重新发布
        {path: '/outlay/statistics',        component: require('@/components/outlay/children/statistics/statistics')},                             // 发布端--重新发布

        {path: '/outlay/query',             component: require('@/components/outlay/children/query/query')},                                       // 发布端--重新发布

        {path: '/outlay/staySettlement',    component: require('@/components/outlay/children/settlement/staySettlement')},                         // 发布端--重新发布
        {path: '/outlay/alreadySettlement', component: require('@/components/outlay/children/settlement/alreadySettlement')},                      // 发布端--重新发布
      ]
    },

    {
      path: '/manage/superManage',//管理端--超级管理员
      component: require('@/components/manage/superManage/superManage'),
      children: [
        {path: '/manage/superManage',              component: require('@/components/manage/superManage/children/userManage')},                             // 管理端--
        {path: '/manage/superManage/userManage',   component: require('@/components/manage/superManage/children/userManage')},                             // 管理端--
        {path: '/manage/superManage/applyManage',  component: require('@/components/manage/superManage/children/applyManage')},                             // 管理端--
        {path: '/manage/superManage/deleteRecord', component: require('@/components/manage/superManage/children/deleteRecord')},                             // 管理端--
        {path: '/manage/superManage/statistics',   component: require('@/components/manage/superManage/children/statistics')},                             // 管理端--
        {path: '/manage/superManage/query',        component: require('@/components/manage/superManage/children/query')},                             // 管理端--
      ]
    },
    {
      path: '/manage/ordinaryManage',//管理端--普通管理员
      component: require('@/components/manage/ordinaryManage/ordinaryManage'),
      children: [
        {path: '/manage/ordinaryManage',                 component: require('@/components/manage/ordinaryManage/children/hospitalManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/hospitalManage',  component: require('@/components/manage/ordinaryManage/children/hospitalManage')},                               // 管理端--
        {path: '/manage/ordinaryManage/expertManage',    component: require('@/components/manage/ordinaryManage/children/expertManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/allocatingManage',component: require('@/components/manage/ordinaryManage/children/allocatingManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/releaseManage',   component: require('@/components/manage/ordinaryManage/children/releaseManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/outlayManage',    component: require('@/components/manage/ordinaryManage/children/outlayManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/CostManage',      component: require('@/components/manage/ordinaryManage/children/CostManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/templateManage',  component: require('@/components/manage/ordinaryManage/children/templateManage')},                             // 管理端--
        {path: '/manage/ordinaryManage/statistics',      component: require('@/components/manage/ordinaryManage/children/statistics')},                             // 管理端--
        {path: '/manage/ordinaryManage/query',           component: require('@/components/manage/ordinaryManage/children/query')},                             // 管理端--
      ]
    }
  ]
})



