<style scoped lang="less">
  .ceachInfo {
    width: 100%;
    height: 100%;
    text-align: left;
    margin-left: 10px;
    li {
      display: inline-block;
      height: 100%;
      line-height: 49px;
      span {
        height: 100%;
        font-size: 14px;
        vertical-align: middle;
      }
      button {
        vertical-align: 2px;
      }
      input {
        vertical-align: 0px;
      }
    }
  }
  @media (max-width: 1500px) {
    .timer {
      display: none !important;
    }
  }
</style>
<template>
  <ul class="ceachInfo">
    <li class="timer">
      <span>申请时间：</span>
      <span>从</span>
      <el-date-picker v-model="seachData.starttime" type="date" size="small" placeholder="选择日期" :style="{width: '120px'}" :picker-options="seachData.starttimepickerOptions"></el-date-picker>
      <span>到</span>
      <el-date-picker v-model="seachData.endtime" type="date" size="small" placeholder="选择日期" :style="{width: '120px'}" :picker-options="seachData.endtimepickerOptions"></el-date-picker>
    </li>
    <li>
      <span>状态：</span>
      <el-select v-model="seachData.ztValue" size="small" :style="{width: '80px'}" placeholder="请选择">
        <el-option v-for="item in seachData.zt" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </li>
    <li>
      <span>系统分类</span>
      <el-select v-model="seachData.flValue" size="small" placeholder="请选择">
        <el-option v-for="item in seachData.fl" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </li>
    <li>
      <span>送检专家：</span>
      <el-select v-model="seachData.zjValue" size="small" :style="{width: '100px'}" placeholder="请选择">
        <el-option v-for="item in seachData.zj" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
    </li>
    <li>
      <el-input v-model="seachData.seachInfo" size="small" placeholder="关键字 如： 姓名" @keyup.enter.native="seach"></el-input>
    </li>
    <li>
      <el-button type="primary" icon="search" size="small" @click="seach">搜索</el-button>
    </li>
  </ul>
</template>
<script>
  export default {
    name: '',
    data(){
      return {

      }
    },
    props: ['seachData'],
    mounted(){

    },
    methods: {
      seach(){
        this.$emit('seach', 'seach')
      }
    }
  }
</script>







