<style scoped lang="less">
  @menu-color:#6babdd;
  .menu {
    width: 100%;
    height: 100%;
    background: @menu-color;
    position: relative;
    .menu-header {
      position: absolute;
      height: 60px;
      line-height: 60px;
      width: 100%;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      padding: 0 1%;
      background: #5296dc;
      color: #fff;
      border-bottom: 1px solid #70aceb;
      display: flex;
      h1 {
        flex: 1;
        text-align: left;
        cursor: default;
      }
      span {
        flex: 1;
        text-align: right;
        cursor: default;
      }
    }
    .menu-main {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 80%;
      height: 50%;
      display: flex;
      .menu-list {
        flex: 2;
      }
      .menu-info {
        flex: 1;
        margin: 5px;
        background: darkslategrey;
      }
    }
  }
  .menu-list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    li {
      flex: 1;
      display: flex;
      .menu-list-item {
        flex: 1;
        margin: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #fff;
      }
    }
  }
</style>
<template>
  <div class="menu">
    <div class="menu-header">
      <h1>LOGO</h1>
      <span>专家端</span>
    </div>
    <div class="menu-main">
      <ul  class="menu-list">
        <li>
          <div class="menu-list-item" :style="{background: '#52aad2'}" @click="toInfo('0')"><h3>{{expertMsg[0].name}}</h3></div>
          <div class="menu-list-item" :style="{background: '#6c97db'}" @click="toInfo('1')">{{expertMsg[1].name}}</div>
        </li>
        <li>
          <div class="menu-list-item" :style="{background: '#d3766d'}" @click="toInfo('2')">{{expertMsg[2].name}}</div>
          <div class="menu-list-item" :style="{background: '#cf8c70'}" @click="toInfo('3')">{{expertMsg[3].name}}</div>
          <div class="menu-list-item" :style="{background: '#71b39a'}" @click="toInfo('4')">{{expertMsg[4].name}}</div>
        </li>
      </ul>
      <div class="menu-info"></div>
    </div>
  </div>
</template>
<script>
  import ptNavData from '../../api/pt-nav.js'
  export default {
    name: 'menu',
    data(){
      return {
        expertMsg: ptNavData.expert
      }
    },
    props: ['a'],
    mounted(){

    },
    methods: {
      toInfo(i){
        this.$router.push({path: this.expertMsg[i].child[0].router})
      }
    }
  }
</script>



