<style scoped lang="less">
.release-nav {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  height: 39px;
  line-height: 39px;
  .release-nav-list {
    display: inline-block;
    padding: 0 30px;
    cursor: default;
    background: #58B7FF;
    color: #F9FAFC;
    &:hover,
    &.release-act{
      background: #1D8CE0;
    }
  }
}
.release-info {
  position: absolute;
  top: 10px + 39px + 10px;
  left: 0;
  bottom: 60px;
  right: 0;
  margin: auto;
}
</style>
<template>
  <div class="release layout">
    <div class="layout-header">
      <h1 @click="toMenu">LOGO</h1>
      <div class="user">
        <div class="user-info">
          <el-dropdown trigger="click"  @command="handleCommand">
            <span class="el-dropdown-link">
              发布端<i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="账号设置">账号设置</el-dropdown-item>
              <el-dropdown-item command="修改密码">修改密码</el-dropdown-item>
              <el-dropdown-item command="退出登录">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="layout-main">
      <ul class="release-nav">
        <router-link class="release-nav-list" activeClass="release-act" v-for="item in navInfo" :key="item" :to="item.router" tag="li">{{item.text}}</router-link>
      </ul>
      <div class="release-info">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  import ptNavData from '../../api/pt-nav.js'
  export default {
    name: 'release',
    data(){
      return {
        navInfo: ptNavData.release[0].child,
      }
    },
    methods: {
      toMenu(){
        this.$router.push({path: '/releaseMenu'})
      },
      handleCommand(command) {
        if(command == "退出登录"){
          this.$router.push({path: '/'})
        }else {
          this.$message('点击了' + command + '没有其他的操作');
        }
      }
    }
  }
</script>



