<style scoped lang="less">
  @import "../../../../css/recordLayout";
  .print {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    background: #fff;
    .printbtn {
      float: left;
      margin-left: 1.5%;
      margin-top: 20px;
      vertical-align: middle;
    }
    .dybl {
      float: left;
      margin-left: 1.5%;
      margin-top: 20px;
      vertical-align: middle;
    }
    .printList {
      margin: 0 auto;
      width: 80%;
      margin-top: 80px;
      background: green;
    }
  }
  .cell {
    text-align: center;
  }
</style>
<template>
  <div class="alreadyRecord content-layout">
    <div class="content-header">
      <div class="content-btn">
        <el-button class="dybl" type="primary" @click="printInfo()" size="small">打 印</el-button>
      </div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark"  style="width: 100%"  @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="a" label="病理号"></el-table-column>
        <el-table-column prop="b" label="姓名"></el-table-column>
        <el-table-column prop="c" label="性别"></el-table-column>
        <el-table-column prop="d" label="送检物"></el-table-column>
        <el-table-column prop="e" label="送检时间" width="150"></el-table-column>
        <el-table-column prop="f" label="病例诊断"></el-table-column>
        <el-table-column prop="g" label="送检医生"></el-table-column>
        <el-table-column prop="h" label="打印状态"></el-table-column>
        <el-table-column label="操作" width="200">
          <template scope="scope">
            <el-button type="text" size="small" @click="lookRecord(scope.$index, tableData)">查看</el-button>
            <el-button type="text" size="small" @click="redistribute(scope.$index, tableData)">重新发布</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="prev, pager, next, jumper"
          :total="1000">
        </el-pagination>
      </div>
    </div>
    <transition name="el-fade-in">
      <look-record v-show="showRecord" :recordData="recordData"  @getChildMessage="closeRecord"></look-record>
    </transition>
  </div>
</template>
<script>
  import headerSearch from '../../../public/header-search'
  import lookRecord from '../../../public/lookRecord'
  export default {
    name: '',
    data(){
      return {
        seachData: {
          starttime: '',
          starttimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          endtime: '',
          endtimepickerOptions: {
            disabledDate(time) {
//            return time.getTime() < Date.now() - 8.64e7;
            }
          },
          zt: [
            {value: '不限',label: '不限'},
            {value: '普通',label: '普通'},
            {value: '加急',label: '加急'},
          ],
          ztValue: '',
          fl: [
            {value: '不限',label: '不限'},
            {value: '全科',label: '全科'},
            {value: '软组织',label: '软组织'},
            {value: '皮肤',label: '皮肤'},
            {value: '消化系统',label: '消化系统'},
            {value: '腹膜及腹膜后',label: '腹膜及腹膜后'},
            {value: '妇产科',label: '妇产科'},
            {value: '骨和关节',label: '骨和关节'},
            {value: '泌尿系统',label: '泌尿系统'},
            {value: '鼻、咽、眼、耳、喉',label: '鼻、咽、眼、耳、喉'},
            {value: '甲状腺',label: '甲状腺'},
            {value: '乳腺',label: '乳腺'},
            {value: '口腔、唇、舌、额骨',label: '口腔、唇、舌、额骨'},
            {value: '呼吸系统',label: '呼吸系统'},
            {value: '男性生殖系统',label: '男性生殖系统'},
            {value: '淋巴造血系统',label: '淋巴造血系统'},
            {value: '神经系统',label: '神经系统'},
            {value: '涎腺',label: '涎腺'},
            {value: '纵隔',label: '纵隔'},
            {value: '肝胆',label: '肝胆'},
            {value: '胸膜',label: '胸膜'},
            {value: '内分泌',label: '内分泌'},
            {value: '胰腺',label: '胰腺'},
            {value: '心血管',label: '心血管'},
          ],
          flValue: '',
          zj: [
            {value: '不限',label: '不限'},
            {value: '鸿琪专家',label: '鸿琪专家'},
            {value: '邵建永',label: '邵建永'},
            {value: '唐录英',label: '唐录英'},
            {value: '郜红益',label: '郜红益'},
            {value: '刘勇',label: '刘勇'},
            {value: '王朝夫',label: '王朝夫'},
            {value: '梅开勇',label: '梅开勇'},
            {value: '路名芝',label: '路名芝'},
            {value: '邵春奎',label: '邵春奎'},
            {value: '吕建民',label: '吕建民'},
          ],
          zjValue: '',
          seachInfo: '',
        },
        tableData: [
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'},
          {a: '00001', b: '李四', c: '男', d: '红细胞', e: '2017-06-07 12:12', f: '发烧', g: '小明', h: '未打印'}
        ],
        currentPage: 1,
        recordData: {},//病历信息
        showRecord: false,//病历信息显示隐藏
        multipleSelection: [],//打印病历选中列表
      }
    },
    props: [''],
    components: {
      headerSearch,
      lookRecord
    },
    mounted(){
      this.seachData.ztValue = this.seachData.zt[0].label
      this.seachData.flValue = this.seachData.fl[0].label
      this.seachData.zjValue = this.seachData.zj[0].label
    },
    methods: {
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },

      seachFn(val){
        console.log(this.seachData)
      },

      lookRecord(i,arr){
        this.showRecord = true;
        this.recordData = {
          radio: '1'
        }
      },
      closeRecord(val) {
        //evtValue 关闭  是子组件传过来的值关闭病历信息组件
        this.showRecord = val
      },
      redistribute(i,arr){
          /*重新发布*/
        this.$prompt('请输入重新发布原因', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /\S/,
          inputErrorMessage: '内容不能为空'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '重新发布成功'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      },


      printInfo(){
          /*打印*/
        var dataInfo = this.multipleSelection;
        if(dataInfo.length){
          console.log(dataInfo);
        }else {
          alert("请选择要打印的数据条目");
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        /*当操作数据复选框的时候开始往数组里面添加删除数据。最终使用时拿到的数据为选中的数组*/
      }
    }
  }
</script>






